body {
  width: 100vw;
  height: 100%;
  margin: 0;
  overflow-y: scroll;
  overflow-x: hidden;
  line-height: 1.3 !important;
  color: black;
}

/* Start https://www.cursors-4u.com */ * {cursor: url(https://cur.cursors-4u.net/cursors/cur-9/cur847.png), auto !important;} /* End https://www.cursors-4u.com */
:root {
  --font-default: 'Yantramanav', sans-serif;
  --fontawesome: "Font Awesome 5 Pro";
  --black: #000000;
  --dark: #091d3e;
  --white: #ffffff;
  --color-primary: #104cba;
  --color-heading: #232323;
  --color-paragraph: #666666;
  --border-default: #e7e7e7;
  --box-shadow-primary: 0px 2px 12px 0px #e7e7e7;
  --box-shadow-secondary: 0 0 25px rgba(0, 0, 0, 0.08);
  --bg-gray: #E7EDF8;
  --bg-gradient: linear-gradient(to right, #104cba, #00ccff, #104cba);
  --bg-gradient-secondary: linear-gradient(to right, #02218f, #0e72e3, #02218f);
}

.scene.one {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 20px;
  right: 40px;
  z-index: 9; /* Below section-three */
}

.sub-heading {
  text-transform: uppercase;
  color: var(--color-primary);
  font-weight: 600;
  background: linear-gradient(to right, var(--color-primary), #00ccff, var(--color-primary));
  -webkit-background-clip: text;
  background-clip: text;
  position: relative;
  text-align: left;
  font-size: 2rem !important;
}

.section {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}

.section-content {
  flex: 1;
  padding: 20px;
  text-align: left;
}
.content-container {
  text-align: center;

}

.section-content.right {
  order: 2;
  text-align: right;
}

.scene-container {
  flex: 1;
  position: relative;
}

.section:nth-child(odd) .section-content {
  order: 1;
}

.section:nth-child(odd) .scene-container {
  order: 2;
}

.section:nth-child(even) .section-content {
  order: 2;
}

.section:nth-child(even) .scene-container {
  order: 1;
}

.section h1 {
  font-size: 8rem;
  margin-bottom: 1rem;
}

.section p {
  font-size: 3rem;
}

h1 {
  font-size: 2em;
  margin-bottom: 20px;
}

p {
  font-size: 1.2em;
}

.section-one, .section-two,.section-four, .section-five {
  background-color: white;
}
.section-three{
  background-color: #ffffffd6;
  padding: 20px;
}
.section-three .scene.one {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}
.section-three {
  position: relative;
  
  flex-direction: column;
  z-index: 10; /* Ensure it's above the model */
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;

  padding: 20px;
}

.card {
  flex: 1;
  min-width: 200px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 300px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card h2 {
  margin-top: 0;
  color: #000000;
}

.card p {
  font-size: 14px;
  color: #666;
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}
.section-five {
  position: relative;
  text-align: center;
  padding: 20px;
  z-index: 10; /* Ensure it's above the model */
}

.arrows {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px; /* Space between the arrows and heading */
}

.arrow-left, .arrow-right {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 24px;
  padding: 10px;
}

.ecosystem-heading {
  flex: 1; /* Allow the heading to take up remaining space */
  font-size: 2rem;
}

.logos {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.portfolio-button {
  margin-top: 20px;
  cursor: pointer;
}
